<template>
	<view class="launches">

		<u-sticky bgColor="#000">
			<u-tabs :list="tabList" :scrollable="false" @change="changeTabs" lineWidth="35" lineColor="#3978bb"
				:activeStyle="{
			            color: '#3978bb',
			            fontWeight: 'bold',
			            transform: 'scale(1.5)'
			        }" :inactiveStyle="{
			            color: '#606266',
			            transform: 'scale(1)'
			        }" itemStyle="height: 100rpx;"></u-tabs>
		</u-sticky>


<!-- <u-loading-icon v-if="showLoading"></u-loading-icon> -->
		<view v-if="tab==0" v-for="(item,index) in LaunchesPastData">
			<view class="card"  @click="toLaunchesPast(index)">
				<view class="tag">
					<uni-tag :mark="true" :text="'#'+item.orbital_launch_attempt_count_year" type="primary" size="small" />
				</view>
				<view class="left">
					<image :src="item.image || 'http://lsky.yizhipao.xyz/i/2022/11/29/63860e69b174b.png'" />
				</view>
				<view class="right">
					<view class="name">
						<text>{{ item.name }}</text> 
					</view>
					<view class="date">
						<uni-dateformat :date="item.net"></uni-dateformat>
						<text class="detail">任务详情</text>
					</view>
				</view>
			</view>
		</view>

		<view v-if="tab==1" v-for="(item,index) in LaunchUpcoming">
			<view class="card" @click="toLaunchesUpcoming(index)">
				<view class="tag">
					<uni-tag :mark="true" :text="'#'+item.orbital_launch_attempt_count_year" type="primary" size="small" />
				</view>
				<view class="left">
					<image :src="item.image || 'http://lsky.yizhipao.xyz/i/2022/11/29/63860e69b174b.png'" />
				</view>
				<view class="right">
					<view class="name">
						<text>{{ item.name }}</text> 
					</view>
					<view class="date">
						<uni-dateformat :date="item.net"></uni-dateformat>
						<!-- <text class="detail">任务详情</text> -->
					</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				LaunchesPastData: [],
				LaunchUpcoming: [],
				tabList: [{
					name: "已发射"
				}, {
					name: "待发射"
				}],
				tab: '0',
				// showLoading:true,
			}
		},
		
		onLoad() {
			// this.requestData();
			this.LaunchUpcoming=getApp().globalData.LaunchUpcoming;
			this.LaunchesPastData=getApp().globalData.LaunchesPastData;
		},
		computed: {
			// reverseLaunchesPastData() {
			// 	return this.LaunchesPastData.reverse();
			// }
		}, 
		methods: {
			//获取数据
			requestData() {
				uni.vrequest({
					url: getApp().globalData.API.LaunchesPast,
					success: (res) => {
						// console.log(res);
						this.LaunchesPastData = res.data
						getApp().globalData.LaunchesPastData = res.data
						console.log(this.LaunchesPastData);
						this.showLoading=false
					}
				});
				uni.vrequest({
					url: getApp().globalData.API.LaunchesUpcoming,
					success: (res) => {
						this.LaunchesUpcomingData = res.data
						getApp().globalData.LaunchesUpcomingData = res.data
						console.log(this.LaunchesUpcomingData);
					}
				});
			},

			//点击tabs
			changeTabs(res) {
				this.tab = res.index;
			},
			
			toLaunchesPast(res){
				uni.navigateTo({
					url: 'LaunchesPast/LaunchesPast?id='+res,
				});
			},
			toLaunchesUpcoming(res){
				uni.navigateTo({
					url: 'LaunchesUpcoming/LaunchesUpcoming?id='+res,
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.launches {
		width: 100%;
		.card {
			width: 80%;
			height: 180rpx;
			display: flex;
			align-items: center;
			flex-direction: row;
			background-color: #171719;
			border-radius: 20rpx;
			margin-top: 35rpx;
			margin-left: 10%;
			position: relative;
			.tag{
				position: absolute;
				left: 0;
				top: 0;
				
			}
			.left {
				width: 120rpx;
				height: 120rpx;
				margin-left: 20rpx;

				image {
					width: 100%;
					height: 100%;
				}
			}

			.right {
				display: flex;
				margin-left: 30rpx;
				flex-direction: column;
				width: 68%;

				.name {

					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					color: #ffffff;
					font-size: 18px;
				}

				.date {
					display: flex;
					justify-content: space-between;
					margin-top: 40rpx;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					color: #7e7e7e;
					font-size: 12px;

					.detail {
						color: #7e7e7e;
					}
				}
			}
		}
	}
</style>
